/* global $ */
import React, { useState, useEffect } from "react";
import "./Header.css";
import { Link } from "react-router-dom";
import { HashLink } from "react-router-hash-link";

export default function Header() {
  return (
    <nav
      id="navbar"
      className={"navbar fixed-top bg-dark navbar-expand-lg navbar-dark p-md-3"}
    >
      <div className="container-fluid">
        <a className="navbar-brand">Mason Watson</a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#toggleMobileMenu"
          aria-controls="toggleMobileMenu"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <i class="bi bi-list"></i>
        </button>
        <div className="collapse navbar-collapse" id="toggleMobileMenu">
          <ul className="navbar-nav text-center me-auto mb-2 mb-lg-0">
            <HashLink className="hlink"
              spy={true}
              smooth={true}
              offset={0}
              duration={500}
              to="/#home"
            >
              <li className="nav-item nav-link">
                  Home
              </li>
            </HashLink>
            <HashLink className="hlink"
              spy={true}
              smooth={true}
              offset={0}
              duration={500}
              to="/#about"
            >
              <li className="nav-item nav-link">
                  About
              </li>
            </HashLink>
            <Link className="link" to="/timeline">
              <li className="nav-item">
                <a className="nav-link" aria-current="page" href="#">
                  Timeline
                </a>
              </li>
            </Link>
            <Link className="link" to="/contact">
              <li className="nav-item">
                <a className="nav-link" aria-current="page" href="#">
                  Contact
                </a>
              </li>
            </Link>
            <Link className="link" to="/projects">
              <li className="nav-item">
                <a className="nav-link" aria-current="page" href="#">
                  My Projects
                </a>
              </li>
            </Link>
          </ul>
          <ul className="navbar-nav navbar-icons ms-auto">
            <li className="nav-item social-icon">
              <a
                className="nav-link"
                aria-current="page"
                href="https://www.linkedin.com/in/masonxw/"
                target="_blank"
                rel="noreferrer"
              >
                <i className="bi bi-linkedin"></i>
              </a>
            </li>
            <li className="nav-item social-icon">
              <a
                className="nav-link"
                aria-current="page"
                href="https://www.instagram.com/masonwatson.py/"
                target="_blank"
                rel="noreferrer"
              >
                <i className="bi bi-instagram"></i>
              </a>
            </li>
            <li className="nav-item social-icon">
              <a
                className="nav-link"
                aria-current="page"
                href="https://twitter.com/mxwcode"
                target="_blank"
                rel="noreferrer"
              >
                <i className="bi bi-twitter"></i>
              </a>
            </li>
            <li className="nav-item social-icon">
              <a
                className="nav-link"
                aria-current="page"
                href="https://github.com/masonwatson"
                target="_blank"
                rel="noreferrer"
              >
                <i className="bi bi-github"></i>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}
