import React, { useState, useEffect } from "react";

function TimelineItem({ tdate, tevent, tdetail }) {
  return (
      <div className="timeline-content">
        <h2 className="timeline-date">{tdate}</h2>
        <h1 className="timeline-event">{tevent}</h1>
        <p className="timeline-detail">{tdetail}</p>
      </div>
  );
}
export default TimelineItem;
